import {
  Accordion,
  Button,
  Container,
  Dropdown,
  InputGroup,
  Nav,
  Navbar,
  Image,
} from "react-bootstrap";

import React, { useState } from "react";
import { elastic as Menu } from "react-burger-menu";
import {
  Arrowrightw,
  Arrowdown,
  Aviator,
  CasinoMenuIcon,
  Deportes,
  Directo,
  Promociones,
  Slots,
  Omnichannel,
  CasinoEnVivo,
} from "../icons";
import CustomAccordionHeader from "./parts/CustomAccordionHeader";

const LOGO =
  new Date() <= new Date("2023-12-30T22:00:00Z") &&
  new Date() >= new Date("2023-12-01T07:00:00Z")
    ? "https://www.codere.es/_catalogs/masterpage/codere/img/christmassLogo.gif"
    : "https://www.codere.es/_catalogs/masterpage/codere/img/logo.svg";

function Header({ device }) {
  const acceder =
    "https://m.apuestas.codere.es/deportes/#/HomePage?openlogin=true";
  const regis = process.env.REACT_APP_REG_URL;
  const prefix = "https://www.codere.es";

  const [hamburger, setHamburger] = useState(false);
  const [onShow, setOnShow] = useState("");

  const data = [
    {
      url: "https://m.apuestas.codere.es/deportes/#/HomePage",
      text: "Deportes",
      icon: Deportes,
    },
    {
      url: "https://m.apuestas.codere.es/deportes/#/DirectosPage",
      text: "En vivo",
      icon: Directo,
    },
    {
      url: "https://m.apuestas.codere.es/deportes/#/SlotsPage",
      text: "Slots",
      icon: Slots,
    },
    {
      url: "https://m.apuestas.codere.es/deportes/#/CasinoPage",
      text: "Casino",
      icon: CasinoMenuIcon,
    },
    {
      url: "https://m.apuestas.codere.es/deportes/#/CasinoEnVivoPage",
      text: "Casino En vivo",
      icon: CasinoEnVivo,
    },
    {
      url: "https://m.apuestas.codere.es/deportes/#/AviatorPage",
      text: "Aviator",
      isAviator: true,
      icon: Aviator,
    },
    {
      url: "https://m.apuestas.codere.es/deportes/#/PromotionsPage",
      text: "Promociones",
      icon: Promociones,
    },
    {
      url: "https://m.apuestas.codere.es/deportes/#/NearestLocalPage",
      text: "Locales",
      icon: Omnichannel,
    },
  ];

  const seo_menu = [
    { name: "Casino", url: "casino" },
    { name: "Casino en Vivo", url: "casino/casino-en-vivo" },
    { name: "Ruleta", url: "casino/ruleta" },
    { name: "Slots", url: "casino/slots" },
    { name: "Blackjack", url: "casino/blackjack" },
    { name: "Jackpots", url: "casino/jackpots" },
    { name: "Eventos Deportivos", url: "eventos-deportivos" },
    { name: "Apuestas Recomendades", url: "cuotas-deportivas" },
  ];

  const hamburgerHandler = () => {
    hamburger ? setOnShow("") : setOnShow("open");
    setHamburger(!hamburger);
  };

  return (
    <Navbar className="header_nav stroke" variant="dark">
      {device ? (
        <div id="father">
          <div id="outer-container">
            <Menu
              id="elastic"
              left
              customCrossIcon={false}
              pageWrapId={"page-wrap"}
              outerContainerId={"outer-container"}
              burgerButtonClassName={onShow}
              isOpen={hamburger}
              onOpen={hamburgerHandler}
              onClose={hamburgerHandler}
            >
              {data.map((d, k) => (
                <a key={k} href={d.url} rel="nofollow" className={"bm-menu"}>
                  <div
                    className={"mobMenuSVGwrapper d-flex align-items-center"}
                  >
                    {React.createElement(d.icon, {
                      className: "mobileMenuIcon",
                    })}
                    {d.text}
                  </div>

                  <div className={"arrowright"}>
                    <Arrowrightw />
                  </div>
                </a>
              ))}

              <Accordion className="hamb-accordion">
                <Accordion.Item eventKey="0">
                  <CustomAccordionHeader>
                    <span style={{ color: "#79c000" }}>¿Cómo jugar?</span>
                  </CustomAccordionHeader>
                  <Accordion.Body>
                    <ul>
                      {seo_menu.map((s, k) => (
                        <li key={k}>
                          <a
                            onClick={hamburgerHandler}
                            href={`${prefix}/${s.url}`}
                          >
                            {s.name}
                            <Arrowrightw />
                          </a>
                        </li>
                      ))}
                    </ul>
                  </Accordion.Body>
                </Accordion.Item>
              </Accordion>
            </Menu>
            <main id="page-wrap"></main>
            <a
              href="https://m.apuestas.codere.es/deportes/#/HomePage"
              rel="nofollow"
            >
              <img src={LOGO} alt="Codere Casino Online en España" />
            </a>
          </div>

          <div id="header_buttons">
            <Button
              href={acceder}
              rel="nofollow"
              className="acceder-button header_btn"
            >
              Acceder
            </Button>
            <Button
              href={regis}
              rel="nofollow"
              className="registrate-button header_btn"
            >
              Regístrate
            </Button>
          </div>
        </div>
      ) : (
        <Container>
          <InputGroup className="header-group-left">
            <a
              href="https://m.apuestas.codere.es/deportes/#/HomePage"
              rel="nofollow"
            >
              <Image
                style={{ maxWidth: 150 }}
                src={LOGO}
                alt="Codere Casino Online en España"
              />
            </a>
            <Nav className="me-auto">
              {data.map((d, k) => {
                return (
                  <a
                    className="text-uppercase"
                    key={k}
                    href={d.url}
                    rel="nofollow"
                  >
                    {d.text}
                  </a>
                );
              })}
            </Nav>
          </InputGroup>

          <div className="header-group-right">
            <Dropdown>
              <Dropdown.Toggle
                className="header_btn"
                id={"dropdownMenuBtnInicio"}
              >
                <Arrowdown />
              </Dropdown.Toggle>
              <Dropdown.Menu>
                {seo_menu.map((s, k) => {
                  return (
                    <Dropdown.Item key={k} href={`${prefix}/${s.url}`}>
                      {s.name}
                    </Dropdown.Item>
                  );
                })}
              </Dropdown.Menu>
            </Dropdown>
            <Button
              href={acceder}
              rel="nofollow"
              // onClick={handleShow}
              className="acceder-button header_btn"
            >
              Acceder
            </Button>
            <Button
              href={regis}
              rel="nofollow"
              className="registrate-button header_btn"
            >
              Regístrate
            </Button>
          </div>
        </Container>
      )}
    </Navbar>
  );
}

export default Header;
